.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 100vh;
  &__title {
    font-size: 4.5rem;
    color: $main-font-color;
    font-weight: 400;
    line-height: 15.75px;
    margin-bottom: 13vh;
    @media (max-width: 600px) {
      font-size: 3.5rem; }
    @media (max-width: 425px) {
      font-size: 2.5rem; } } }

