.nav {
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  width: 35%;
  margin: 43px auto 0;
  @media(max-width:1440px) {
    width: 45%; }
  @media(max-width:1024px) {
    width: 60%; }
  @media(max-width:991px) {
    width: 75%; }
  @media(max-width:600px) {
    width: 95%;
    margin: 10px auto; }
  &__link {
    font-family: "Josefin Sans";
    color: $white;
    font-weight: 400;
    letter-spacing: 2.6px;
    text-decoration: none;
    font-size: 13px;
    text-transform: uppercase;
    line-height: 18px;
    @media (max-width: 600px) {
      font-size: 10px;
      display: none; } }
  &__hamburger {
    display: none;
    cursor: pointer;
    color: #fff;
    padding: 5px;
    text-decoration: none;
    &:hover {
      background-color: rgba(255,255,255,0.5); }
    @media (max-width: 600px) {
      display: block;
      margin-right: 20px; } } }

.nav.active a {
  display: block; }

